import React from "react";
import Container from "react-bootstrap/Container";

const Footer = (props) => {
  const bgStyle = { backgroundColor: "#f5f5f5" };

  return (
    <footer style={bgStyle} className="mt-auto py-5 text-right ">
      <Container>
        {props.children}
        <i className="fas fa-hammer" /> by{" "}
        <a
          className="badge badge-dark"
          target=" _blank"
          rel="noopener"
          href="https://www.linkedin.com/in/bobmwangih"
          aria-label="My GitHub"
        >
          Mufasa
        </a>
      </Container>
    </footer>
  );
};

export default Footer;
