import React from "react";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Skeleton from "react-loading-skeleton";

const ExperienceCard2 = ({ data }) => {
    const {thumbnail,title,pubDate,guid,categories,author,link} = data;
  return (
    <Col md={6}>
      <Card  className="card shadow-lg p-3 mb-5 bg-white rounded" >
      {/* <Card.Img variant="top" src={thumbnail} width ="auto" height="200" /> */}
        <Card.Body>
          <Card.Title as="h5">{(!title)?"":title || <Skeleton />} </Card.Title>
         
          {guid ? <CardButtons guid={guid} /> : <Skeleton count={2} />}
          <hr />
          {categories ? (
            <CategorySection categories={categories} guid={guid} />
          ) : (
            <Skeleton count={3} />
          )}
          {author ? (
            <CardFooter writer={author} publishedOn={pubDate}/>
          ) : (
            <Skeleton />
          )}
        </Card.Body>
      </Card>
    </Col>
  );
};

const CardButtons = ({ guid }) => {
  return (
    <>
      <a
        href={`${guid}`}
        target=" _blank"
        className="btn btn-outline-secondary mr-3"
      >
        <i className="fab fa-medium" /> Read Blog
      </a>
    </>
  );
};

const CategorySection = ({ categories, guid }) => {

  return (
    <div className="pb-3">
      Categories:{" "}
      {categories
        ? categories.map((category,index) => (
            <a
              key={index} 
              className="badge badge-light card-link"
              href="#"
            >
              {category}{"  "}
            </a>
          ))
        : " "}
    </div>
  );
};

const CardFooter = ({ writer, publishedOn }) => {
  return (
    <p className="card-text">
      <a
        href="https://medium.com/@emaureeny/"
        target=" _blank"
        className="text-dark text-decoration-none"
      >
        <span className="text-dark card-link mr-4">
          <i className="fab fa-medium" /> Author :{" "}
          <span className="badge badge-dark">{writer}</span>
        </span>
      </a>
      <small className="text-muted">published on : {publishedOn}</small>
    </p>
  );
};

export default ExperienceCard2;
